@font-face {
    font-family: "crimson-pro";
    src: url("/assets/fonts/subset-CrimsonPro-Italic.woff2") format("woff2"),
        url("/assets/fonts/subset-CrimsonPro-Italic.woff") format("woff");
    font-weight: normal;
    font-style: italic;
}

:root {
    --color-3: #A85D65;
}

html {
    background: #F7F7F7;
    color: #373737;
    font-family: "crimson-pro", Georgia, 'Times New Roman', Times, 'SongTi TC', serif;
}

body {
    display: grid;
    grid-template-areas:
    "headerimg"
    "header"
    "main"
    "footer";
    grid-template-rows: auto auto 1fr auto;
}

main {
    font-size: 1em;
}

footer {
    font-size: 0.8em;
    text-transform: none;
    letter-spacing: 0;
    font-weight: normal;
}

footer, #poem-attribution {
    font-style: italic;
}

header {
    margin-top: 0;
}

h1 {
    font-size: 4em;
    writing-mode: vertical-rl;
    display: inline-block;
    letter-spacing: 0.2em;
}

#header-img {
    grid-area: headerimg;
    margin-top: 5em;
}

@media (min-width: 800px) {
    body {
        grid-template-areas:
        "headerimg headerimg"
        "header main"
        "poemattribution main"
        "footer footer";
        grid-template-rows: auto auto 1fr auto;
        grid-template-columns: 4fr 5fr;
        column-gap: 5em;
    }

    header {
        text-align: right;
    }

    h1 {
        margin-bottom: 0;
    }

    h1, h1 img {
        height: 4em;
    }

    main {
        display: contents;
    }

    #header-img {
        margin-top: 9em;
        justify-self: center;
    }

    #poem-main {
        grid-area: main;
    }

    #poem-attribution {
        grid-area: poemattribution;
        justify-self: end;
        // margin-top: 0;
        text-align: right;
    }
}